import * as React from "react"
import { Link } from "gatsby"

const Layout = ({ location, title, children }) => {
  const rootPath = `${__PATH_PREFIX__}/`
  const isRootPath = location.pathname === rootPath
  let header

  if (isRootPath) {
    header = (
      <h1 className="main-heading">
        <Link to="/">{title}</Link>
      </h1>
    )
  } else {
    header = (
      <Link className="header-link-home" to="/">
        {title}
      </Link>
    )
  }

  return (
    <div className="global-wrapper" data-is-root-path={isRootPath}>
      <header className="global-header">{header}</header>
      <main>{children}</main>
      <footer>
 <p xmlnscc="http://creativecommons.org/ns#" xmlnsdct="http://purl.org/dc/terms/"><span property="dct:title" style={{fontWeight:"bold"}}>Ogre3DLearningJourney</span> by <span style={{fontWeight:"bold"}} property="cc:attributionName">EmVee381</span> is licensed under <a href="http://creativecommons.org/licenses/by-nc-nd/4.0/?ref=chooser-v1" target="_blank" rel="license noopener noreferrer" style={{display:"inline-block"}}>CC BY-NC-ND 4.0<img style={{height:22,marginLeft:"3px",verticalAlign:"text-bottom"}} src="https://mirrors.creativecommons.org/presskit/icons/cc.svg?ref=chooser-v1"/><img style={{height:22,marginLeft:"3px",verticalAlign:"text-bottom"}} src="https://mirrors.creativecommons.org/presskit/icons/by.svg?ref=chooser-v1"/><img style={{height:22,marginLeft:"3px",verticalAlign:"text-bottom"}} src="https://mirrors.creativecommons.org/presskit/icons/nc.svg?ref=chooser-v1"/><img style={{height:22,marginLeft:"3px",verticalAlign:"text-bottom"}} src="https://mirrors.creativecommons.org/presskit/icons/nd.svg?ref=chooser-v1"/></a> 
                                                                                                                      <b> {new Date().getFullYear()}</b>, Built with
        {` `}
        <a href="https://www.gatsbyjs.com">Gatsby</a></p>
      </footer>
    </div>
  )
}

export default Layout
